/* Existing CSS */

.cards {
  padding: 4rem;
  background: #fff;
}

.cards2 {
  padding: 0 4rem 4rem 4rem;
  background: #fff;
}

h1 {
  text-align: center;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1320px;
  width: 100%;
  margin: 0 auto;
}
.cards__text {
  font-size: 1.5rem; /* Ajuster la taille du texte selon vos besoins */
  line-height: 1.6; /* Ajuster l'interligne pour une meilleure lisibilité */
  text-align: center; /* Centrer le texte */
  margin: 20px 0; /* Ajouter des marges si nécessaire */
}


.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #e50f85;
  box-sizing: border-box;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the image covers the entire container */
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 20px 30px 30px;
}

.cards__item__text {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
}

.imgsize {
  width: 357px;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}

@media only screen and (max-width: 960px) {
  .imgsize {
    width: 257px;
  }
}

/* New CSS for Purpose Section */

.purpose-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.purpose-text {
  flex: 1;
  padding-right: 20px;
  font-size: 1.5rem; /* Ajuster la taille du texte */
  line-height: 1.6;  /* Optionnel: ajuster l'interligne pour une meilleure lisibilité */
}

.purpose-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.purpose-image img {
  max-width: 100%;
  height: auto;
}

@media only screen and (max-width: 960px) {
  .purpose-container {
    flex-direction: column;
    text-align: center;
  }

  .purpose-text {
    padding-right: 0;
    margin-bottom: 20px;
  }
}
